.wrapper {
    width: 650px;
    margin: 0px auto;


}
.browser {
    margin: 50px;
    padding: 30px 0px 0px 60px;
    height: 680px;
    background: linear-gradient(45deg, rgba(45,176,224,1) 0%, rgba(96,40,168,1) 100%);
    border-radius: 50px;
}
.info {
    color: white;
}
.heading {
    font-size: 30px;
    padding: 0px 0px 0px 60px;
}
.infoSiti {
    font-size: 26px;
    padding: 0px 0px 0px 60px;
}

.weather {
    color: white;
    font-size: 24px;
    padding: 0px 0px 0px 60px;
}
.inpt {
    height: 35px;
    width: 200px;
    border-radius: 10px;
    margin: 0px 0px 30px 100px;
    font-size: 22px;

}
.sun {
    margin: -230px 0px 0px 310px;
}
.butt {
    height: 40px;
    width: 200px;
    border-radius: 10px;
    font-size: 22px;
    margin: 0px 0px 0px 100px;
    background-color: darkslateblue;
    color: white;
}